<template>
<div>
   <section class="head">
      <div> <a @click="goBack" class="iconfont back"> &#xe614; </a> </div>
      <h1>Edit Profile</h1>
      <div class="chartWrap">
        </div>
    </section>
    <div class="formWrap">
        <p>Name</p>
        <van-field label="Alice" v-model="value" />
        <div class="sex" @click="showName()">
            <van-field label="Gender" disabled right-icon="arrow-down" v-model="value" />
            <div class="witch" v-show="control">
                <p>Men</p>
                <p>Women</p>
            </div>
        </div>
        
        <p class="tips">You can change it only one time. It will be visible on Shopeone</p>
        <van-field label="User Name" v-model="value" />
        <div class="email">
            <van-field label="Email" v-model="value" />
            <span class="change">Change</span>
        </div>
        <div class="coat"><div class="label">Nearest Landmark</div> <van-field v-model="value" /><span class="add">Add</span></div>
        <van-button type="danger">Update Profile</van-button>
        <span class="reset">Reset Password</span>
    </div>

</div>
</template>

<script>


export default {
  name: 'editProfile',
  data () {
    return {
     control: false,
    
    }
  },
  computed: {
  },
  mounted() {
    this.init();
  },
  methods: {
    init(){

    },
    changeSort(val){
      this.sortValue=val;
      this.sortVisible=false;
    },
    goBack(){
      this.$router.back(-1)
    },
    showName(){
        this.control = true;
    }

  }
}
</script>

<style lang="scss" scoped>
   .formWrap{
       width: 100%;
       padding-right: 4%;
       border-top: 10px solid #f6f6f6;
       .coat{
           width: 100%;
           padding: 0 0 0 4%;
           display: -webkit-box;
           border-bottom: #f6f6f6 1px solid;
           .label{
               color: #999999;
               display: flex; align-items: center;
           }
           .codeName{
               display: flex; align-items: center;
               padding-right: 10px;
           }
           .van-cell{
                   padding: 10px 0 10px 15px;
                   width: 60%;
            }
            .add{
                width: 8%;
                display: flex; align-items: center;
                color: #2B87D5;
            }      
       }
       p{
           font-size: 10px;
           color: #999999;
           padding: 5px 0 0 15px;
       } 
       .tips{
           font-size: 11px;
           color: #666666;
       }
       .email{
           width: 100%;
           display: -webkit-box;
           .van-cell{
               width: 85%;
           }
           .change{
               width: 15%;
               display: flex; align-items: center;justify-content: flex-end;
               color: #2B87D5;
               border-bottom: #f6f6f6 1px solid;
           }
       }
   }
   .van-cell{
       padding: 10px 15px 10px 15px;
       color: #999999;
   }
   .van-cell:nth-child(2){
       padding-top: 0;
   }
   .van-cell:nth-child(3){
       padding-right: 0;
   }
   .van-cell:nth-child(5){
       padding-top: 0;
   }
   .van-button--danger{
        width: 96%;
        margin-left: 4%;
        margin-top: 65px;
    }
    .reset{
        width: 96%;
        display: block;
        margin-left: 4%;
        color: #2B87D5;
        font-size: 12px;
        text-align: center;
        padding-top: 10px;
    }
    .sex{
        width: 100%;
        position: relative;
        .witch{
            position: absolute;
            right: 20px; 
            background: #fff; 
            border-radius: 5px; 
            box-shadow: 0 0 5px 2px rgba(23,23,23,.2); 
            padding: 5px 5px 10px; 
            z-index: 1000;
            height: 90px;
            p{
                padding:10px 10px; 
                text-align: center; 
                white-space: normal; 
                min-width: 120px;
                font-size: 14px;
            }
        }
    }
</style>






